import React, { useState, useEffect, useMemo } from 'react';
import axios from 'axios';
import Particles, { initParticlesEngine } from '@tsparticles/react';
import { loadSlim } from '@tsparticles/slim';


const StaticParticles = React.memo(function StaticParticles() {
  const options = useMemo(() => ({
    fullScreen: { enable: false },
    background: { color: { value: '#0a1128' } }, //  
    particles: {
      number: { value: 60, density: { enable: true } },
      color: { value: '#ffffff' },
      opacity: { value: 0.5 },
      links: { enable: true, distance: 150, opacity: 0.3, color: '#ffffff' },
      move: { enable: true, speed: 1.2 },
    },
    interactivity: {
      events: {
        onHover: { enable: true, mode: 'repulse' },
        resize: { enable: true },
      },
      modes: { repulse: { distance: 140, duration: 0.4, speed: 1 } },
    },
    detectRetina: true,
  }), []); //    

  return <Particles id="tsparticles" options={options} className="canvas" />;
});


// API  URL
const API_URL = 'https://narnia-events.onrender.com/api';
const TIME_SLOT_LIMIT = 10; //   

// 
interface FormData {
  studentName: string;
  parentContact: string;
  schoolLevel: '' | '' | '' | '';
  grade: string;
  interest: string;
}

//   
type TimeSlotButtonProps = {
  time: string;
  active: boolean;
  disabled: boolean;
  onClick: () => void;
};

const TimeSlotButton: React.FC<TimeSlotButtonProps> = ({ time, active, disabled, onClick }) => (
  <button
    type="button"
    onClick={onClick}
    disabled={disabled}
    className={`time-slot-btn ${active ? 'active' : ''}`}
    style={{ padding: '8px 12px', margin: 4, borderRadius: 8, border: '1px solid #dddddd', opacity: disabled ? 0.6 : 1 }}
  >
    {time} {disabled ? '()' : ''}
  </button>
);

function App() {
  const [init, setInit] = useState(false);

  const [selectedDate] = useState<string>(new Date().toISOString().split('T')[0]);
  const [bookedTimes, setBookedTimes] = useState<string[]>([]);
  const [selectedTime, setSelectedTime] = useState<string | null>(null);
  const [formData, setFormData] = useState<FormData>({
    studentName: '',
    parentContact: '',
    schoolLevel: '',
    grade: '',
    interest: '',
  });
  const [message, setMessage] = useState<{ type: 'success' | 'danger'; text: string } | null>(null);

  // tsparticles 1 
  useEffect(() => {
    initParticlesEngine(async (engine) => {
      await loadSlim(engine);
    }).then(() => setInit(true));
  }, []);

  //   
  const timeSlots = [
    '10:30', '11:00', '11:30', '12:00', '12:30', '13:00', '13:30',
    '14:00', '14:30', '15:00', '15:30', '16:00',
  ];

  //     
  useEffect(() => {
    // The backend is not available on GitHub Pages, so we don't fetch.
    // You can add mock data here if needed.
  }, [selectedDate]);

  //   
  const handleFormChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  //  
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedTime) {
      setMessage({ type: 'danger', text: '  .' });
      return;
    }

    const currentSlotReservationsCount = bookedTimes.filter((time) => time === selectedTime).length;
    if (currentSlotReservationsCount >= TIME_SLOT_LIMIT) {
      setMessage({ type: 'danger', text: `. ${selectedTime}    .` });
      return;
    }

    const reservationData = {
      ...formData,
      date: selectedDate,
      time: selectedTime,
    };

    axios
      .post(`${API_URL}/reservations`, reservationData)
      .then(() => {
        setBookedTimes((prev) => [...prev, selectedTime]);
        setSelectedTime(null);
        setFormData({ studentName: '', parentContact: '', schoolLevel: '', grade: '', interest: '' });
        setMessage({ type: 'success', text: '  !' });
      })
      .catch((error) => {
        console.error('Error creating reservation:', error);
        if ((error as any).response && (error as any).response.status === 409) {
          setMessage({ type: 'danger', text: '  .   .' });
        } else {
          setMessage({ type: 'danger', text: '   .' });
        }
      });
  };

  const handleScrollTo = (id: string) => {
    document.getElementById(id)?.scrollIntoView({ behavior: 'smooth' });
  };

  //      
  const countByTime = timeSlots.reduce<Record<string, number>>((acc, t) => {
    acc[t] = bookedTimes.filter((x) => x === t).length;
    return acc;
  }, {});

  return (
  <div className="app-wrap">
    {init && <StaticParticles />}





    <div className="app-inner">
      <header className="app-header">
        <h1 className="app-title">  </h1>
        <nav className="app-nav">
          <button type="button" className="btn" onClick={() => handleScrollTo('times')}></button>
          <button type="button" className="btn" onClick={() => handleScrollTo('form')}></button>
        </nav>
      </header>

      {message && (
        <div className={`alert ${message.type === 'success' ? 'alert-success' : 'alert-danger'}`} role="alert">
          {message.text}
        </div>
      )}

      <section id="times" className="section">
        <h2>  </h2>
        <div className="time-grid">
          {timeSlots.map((t) => {
            const isFull = (countByTime[t] ?? 0) >= TIME_SLOT_LIMIT;
            return (
              <TimeSlotButton
                key={t}
                time={t}
                active={selectedTime === t}
                disabled={isFull}
                onClick={() => setSelectedTime(t)}
              />
            );
          })}
        </div>
        <p style={{ color: '#666', marginTop: 8 }}>
          *    {TIME_SLOT_LIMIT}   .  : {selectedTime ?? ''}
        </p>
      </section>

      <section id="form" className="section">
        <h2> </h2>
        <form onSubmit={handleSubmit} className="form">
          <div className="form-grid">
            <div className="form-group">
              <label htmlFor="studentName"> </label>
              <input
                type="text"
                id="studentName"
                name="studentName"
                value={formData.studentName}
                onChange={handleFormChange}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="parentContact"> </label>
              <input
                type="tel"
                id="parentContact"
                name="parentContact"
                value={formData.parentContact}
                onChange={handleFormChange}
                required
              />
              <p style={{ fontSize: '12px', color: '#666', marginTop: '4px' }}>'-'   .</p>
            </div>
            <div className="form-group">
              <label htmlFor="schoolLevel"></label>
              <select
                id="schoolLevel"
                name="schoolLevel"
                value={formData.schoolLevel}
                onChange={handleFormChange}
                required
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>
            <div className="form-group">
              <label htmlFor="grade"></label>
              <input
                type="text"
                id="grade"
                name="grade"
                value={formData.grade}
                onChange={handleFormChange}
                required
              />
              <p style={{ fontSize: '12px', color: '#666', marginTop: '4px' }}>: 1, 2</p>
            </div>
            <div className="form-group full-width">
              <label htmlFor="interest">  </label>
              <input
                type="text"
                id="interest"
                name="interest"
                value={formData.interest}
                onChange={handleFormChange}
                required
              />
              <p style={{ fontSize: '12px', color: '#666', marginTop: '4px' }}>: , ,   </p>
            </div>
          </div>
          <button type="submit" className="submit" disabled={!selectedTime}>
            {selectedTime ? `${selectedTime} ` : '  '}
          </button>
        </form>
      </section>
    </div>
  </div>
);
}
export default App;
